
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
}
body {
  background: linear-gradient(
    90deg,
    rgba(48, 16, 255, 1) 0%,
    rgba(100, 115, 255, 1) 100%
  );
  /* overflow: hidden; */
}
.App {
  background: #161a2b;
  text-align: center;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  width: 40%;
  margin: 2.5% auto;
  height: 90vh;
  border-radius: 8px;
}
h1 {
  color: white;
  font-size: 2rem;
  padding-top: 0.5em;
  padding-bottom: 0.5em;
}
input {
  outline: none;
  border: 2px solid #5d0cff;
  padding: 14px 32px 14px 16px;
  background: transparent;
  width: 28vw;
  border-radius: 4px 0 0 4px;
  color: white;
}
.add button,
.filter button {
  outline: none;
  border: none;
  padding: 16px;
  background: linear-gradient(
    90deg,
    rgba(93, 12, 255, 1) 0%,
    rgba(155, 0, 250, 1) 100%
  );
  color: #fff;
  border-radius: 0 4px 4px 0;
}
.filter {
  margin: 20px 25px 10px 25px;
}
.task {
  color: white;

  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 35vw;
  border-radius: 6px;
  border: none;
  margin: 20px auto;
  background: linear-gradient(
    90deg,
    rgba(255, 118, 20, 1) 0%,
    rgba(255, 84, 17, 1) 100%
  );
  /* height: 40px; */
  padding: 10px 10px;
}

.task button,
.edit-btn {
  background: transparent;
  border: 2px solid #fff;
  color: #fff;
  width: 4.5rem;
  height: 2rem;
  transition: all 0.5s;
}
.task button:hover {
  background: wheat;
  color: #000;
}
.edit-btn:hover {
  background: wheat;
  color: #000;
}

.task:nth-child(4n + 1) {
  background: linear-gradient(
    90deg,
    rgba(93, 12, 255, 1) 0%,
    rgba(155, 0, 250, 1) 100%
  );
}

.task:nth-child(4n + 2) {
  background: linear-gradient(
    90deg,
    rgba(255, 12, 241, 1) 0%,
    rgba(250, 0, 135, 1) 100%
  );
}

.task:nth-child(4n + 3) {
  background: linear-gradient(
    90deg,
    rgba(20, 159, 255, 1) 0%,
    rgba(17, 122, 255, 1) 100%
  );
}
@media screen and (max-width: 900px) {
  .App {
    width: 100%;
    height: 100vh;
    border: none;
    margin: 0;
    border-radius: 0;
  }
  input {
    border: 2px solid #5d0cff;
    padding: 7px 16px 7px 8px;
    background: transparent;
    width: 68vw;
    border-radius: 4px 0 0 4px;
    color: white;
  }
  .add button
  .filter button {
    padding: 9px;
    border-radius: 0 2px 2px 0;
  }
  .task {
    color: white;
    font-size: 1em;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 90vw;
    border-radius: 6px;
    border: none;
    margin: 12px auto;
    background: linear-gradient(
      90deg,
      rgba(255, 118, 20, 1) 0%,
      rgba(255, 84, 17, 1) 100%
    );
    /* height: 40px; */
    padding: 5px 5px;
  }
  .task button {
    background: transparent;
    border: 2px solid #fff;
    color: #fff;
    width: 4rem;
    height: 2rem;
    transition: all 0.5s;
    font-size: 1em;
  }
}